body {
  text-align: center;
  background-color: #eee;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 1920px;
  margin: 0 auto;
}

.is-hide {
  display: none;
}

.search-form {
  max-width: 500px;
  margin: 0 auto 20px;
  display: flex;
}

input[type="text"] {
  border: 1px solid #4976ff;
  border-radius: 10px;
  outline: rgba(0, 0, 0, 0);
  flex: 1;
  margin-right: 20px;
  padding: 8px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #ed6d19;
  border: none;
  border-radius: 10px;
  padding: 8px 16px;
}

button:hover {
  box-shadow: 0 1px 3px rgba(0, 0, 0, .29);
}

button:active {
  box-shadow: 0 0 rgba(0, 0, 0, .29);
}

.load-more {
  margin: 20px auto;
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 10px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 10px;
  padding: 10px 10px 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.photo-card:focus, .photo-card:hover {
  transform: scale(105%);
}

.photo-card img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.info {
  color: #2949aa;
  justify-content: space-between;
  font-size: 12px;
  display: flex;
}

.info-item {
  margin: 0;
}

.sl-overlay {
  background: #000;
}

.sl-close, .sl-navigation button {
  color: #ed6d19;
}

